import { StaticQuery, graphql } from "gatsby";
import React from "react";
import { css } from "@emotion/core";
import Dropdown from "./dropdown";

class Navbar extends React.Component {
  render() {
    return (
      <nav
        css={css`
          float: right;
          margin: 0;
          height: inherit;
          @media (max-width: 600px) {
            &:hover ul {
              display: block;
            }
          }
        `}
      >
        <button
          css={css`
            display: none;
            width: 40px;
            height: 40px;
            @media (max-width: 600px) {
              display: inline-block;
            }
          `}
        >
          üçî
        </button>
        <ul
          css={css`
            padding: 0 0;
            list-style: none;
            display: inline;
            @media (max-width: 600px) {
              display: none;
              position: absolute;
              padding: 1px;
              background: #000;
              right: 0px;
              top: 1.5rem;
              width: 100%;
            }
          `}
        >
          {this.props.data.allDirectory.edges.map(({ node }) => (
            <Dropdown directoryNode={node} key={node.id} />
          ))}
          <li
            css={css`
              float: left;
              margin-right: 1px;
              background-color: black;
              &:hover {
                background-color: #24890d;
              }
              line-height: 2.5rem;
              @media (max-width: 600px) {
                text-align: center;
                width: 100%;
                padding: 10px 0;
                margin: 0;
              }
            `}
          />
        </ul>
      </nav>
    );
  }
}

export default () => (
  <StaticQuery
    query={graphql`
      query {
        allDirectory(
          filter: {
            sourceInstanceName: { eq: "content" }
            relativePath: { regex: "/^[^/]+$/" }
            name: { regex: "/^[^/_]/" }
          }
          sort: { order: ASC, fields: [name] }
        ) {
          edges {
            node {
              id
              name
            }
          }
        }
      }
    `}
    render={data => <Navbar data={data} />}
  />
);
